"use client";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { MarkdownMessage } from "./MarkdownMessage";
import { motion, AnimatePresence } from "framer-motion";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Spinner } from "@/components/ui/spinner";
import { Textarea } from "@/components/ui/textarea";
import { ChatService } from "@/services/chatService";
import { useProjectStore } from "@/store/useProjectStore";
import {
  Bot,
  FolderOpen,
  MoreVertical,
  Plus,
  Send,
  Trash2,
} from "lucide-react";
import { useState, useEffect, useRef } from "react";
import { toast } from "sonner";
import { ChatMessage } from "./ChatMessage";
import { TypingIndicator } from "./TypingIndicator";

declare global {
  interface Window {
    injectTerminalCommand?: (command: string) => void;
  }
}

export function ChatInterface() {
  const [inputValue, setInputValue] = useState("");
  const {
    chatSessions,
    activeChatId,
    isTyping,
    addMessageToActiveChat,
    setIsTyping,
    activeAgents,
    availableAgents,
    toggleAgent,
    clearMessages,
    createNewChatSession,
    project,
    openTabs,
    uploadFile,
    openFile,
    loadFiles,
    setActiveArea,
  } = useProjectStore();

  const [streaming, setStreaming] = useState(false);
  const [streamedText, setStreamedText] = useState("");
  const [isStreaming, setIsStreaming] = useState(false);
  const bottomRef = useRef<HTMLDivElement>(null);

  // Ensure 'Coder' agent is default and active on mount or when availableAgents changes
  useEffect(() => {
    if (Array.isArray(availableAgents) && availableAgents.length > 0) {
      const coderAgent = availableAgents.find(
        (a) => a.name?.toLowerCase() === "coder"
      );
      if (coderAgent && !coderAgent.isActive) {
        toggleAgent(String(coderAgent.id));
      } else if (!coderAgent) {
        // If no 'Coder', activate the first agent if none are active
        const anyActive = availableAgents.some((a) => a.isActive);
        if (!anyActive) {
          toggleAgent(String(availableAgents[0].id));
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [availableAgents]);

  const activeChat = chatSessions.find((chat) => chat.id === activeChatId);
  const messages = activeChat?.messages || [];

  // Auto-scroll to bottom on new message or typing state change
  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages.length, isTyping, streamedText]);

  // Handle slash commands like /clear, /reset, /help
  const handleCommand = (command: string) => {
    const cmd = command.trim().toLowerCase();
    if (cmd === "/clear") {
      clearMessages();
      toast.success("Chat cleared");
    } else if (cmd === "/reset") {
      createNewChatSession("New Chat");
      toast.success("Chat reset");
    } else if (cmd === "/help") {
      toast.info("Commands: /clear, /reset, /help");
    } else {
      toast.error(`Unknown command: ${cmd}`);
    }
  };

  const handleSendMessage = async () => {
    if (!inputValue.trim()) return;

    // Check if it's a command
    if (inputValue.startsWith("/")) {
      handleCommand(inputValue);
      setInputValue("");
      return;
    }

    addMessageToActiveChat({
      id: Math.random().toString(36).substring(7),
      content: inputValue,
      sender: "user",
      timestamp: new Date(),
      type: "text",
    });
    const messageToSend = inputValue;
    setInputValue("");
    setIsTyping(true);
    try {
      const activeAgentIds = (Array.isArray(activeAgents) ? activeAgents : [])
        .filter((a) => a.isActive)
        .map((a) => a.id);
      const data = await ChatService.sendMessage({
        chatId: activeChatId || "default-chat",
        projectId: project?.id || "default-project",
        message: messageToSend,
        activeAgents: activeAgentIds,
      });
      for (const msg of data.messages) {
        let content = "";
        if (
          msg &&
          "choices" in msg &&
          Array.isArray(msg.choices) &&
          msg.choices.length > 0 &&
          msg.choices[0].message &&
          typeof msg.choices[0].message.content === "string"
        ) {
          content = msg.choices[0].message.content ?? "";
        } else if (typeof msg.content === "string") {
          content = msg.content;
        }
        addMessageToActiveChat({
          id: Math.random().toString(36).substring(7),
          content: content ?? "",
          sender: "ai",
          agentName: String(
            msg && "agentName" in msg && typeof msg.agentName === "string"
              ? msg.agentName
              : "AI"
          ),
          timestamp: new Date(),
          type: "text",
        });
      }
      if (data.files && Array.isArray(data.files)) {
        for (const file of data.files) {
          await uploadFile({ filePath: file.filePath, content: file.content });
          openFile({
            id: Math.random().toString(36).substring(7),
            name: file.filePath.split("/").pop() || file.filePath,
            path: file.filePath,
            content: file.content,
            language: file.filePath.split(".").pop() || "text",
            type: "file",
          });
        }
        if (project?.id) await loadFiles(project.id);
      }
      toast.success("Message sent to agent(s)");
    } catch (error) {
      console.error("Chat API error:", error);
      addMessageToActiveChat({
        id: Math.random().toString(36).substring(7),
        content: "Failed to get AI response. Please try again.",
        sender: "ai",
        agentName: "System",
        timestamp: new Date(),
        type: "text",
      });
      toast.error("Failed to send message to agent(s)");
    } finally {
      setIsTyping(false);
    }
  };

  const handleNewChat = () => {
    createNewChatSession("New Chat");
  };

  const handleClearChat = () => {
    clearMessages();
  };

  const handleSendMessageStream = async () => {
    if (!inputValue.trim()) return;
    setStreamedText("");
    setIsStreaming(true);
    const messages = [
      { role: "system", content: "You are a helpful AI developer assistant." },
      { role: "user", content: inputValue },
    ];
    try {
      const stream = await ChatService.sendMessageStream({ messages });
      const reader = stream.getReader();
      const decoder = new TextDecoder("utf-8");
      let done = false;
      let buffer = "";
      while (!done) {
        const { value, done: doneReading } = await reader.read();
        done = doneReading;
        if (value) {
          buffer += decoder.decode(value, { stream: true });
          // Parse SSE: split on double newlines
          const parts = buffer.split("\n\n");
          buffer = parts.pop() || "";
          for (const part of parts) {
            if (part.startsWith("data: ")) {
              const chunk = part.replace("data: ", "");
              if (chunk === "[DONE]") {
                setIsStreaming(false);
                return;
              }
              setStreamedText((prev) => prev + chunk);
            }
          }
        }
      }
    } catch (err) {
      setIsStreaming(false);
      toast.error("Streaming error: " + (err as Error).message);
    }
    setIsStreaming(false);
  };

  const handleWorkflow = async () => {
    if (!inputValue.trim()) return;
    addMessageToActiveChat({
      id: Math.random().toString(36).substring(7),
      content: inputValue,
      sender: "user",
      timestamp: new Date(),
      type: "text",
    });
    const messageToSend = inputValue;
    setInputValue("");
    setIsTyping(true);
    try {
      const context = openTabs.map((tab) => ({
        path: tab.path,
        content: tab.content,
        language: tab.language,
      }));
      const data = await ChatService.sendWorkflowMessage({
        chatId: activeChatId || "default-chat",
        message: messageToSend,
        projectId: project?.id || "default-project",
        context,
      });
      if (Array.isArray(data.responses)) {
        for (const resp of data.responses) {
          addMessageToActiveChat({
            id: Math.random().toString(36).substring(7),
            content: resp.content,
            sender: "ai",
            agentName: resp.agentName,
            timestamp: new Date(),
            type: "text",
          });

          const terminalCommand = extractTerminalCommand(resp.content);
          if (terminalCommand) {
            setActiveArea("terminal");
            if (typeof window !== "undefined" && window.injectTerminalCommand) {
              window.injectTerminalCommand(terminalCommand);
            }
          }

          if (resp.agentName?.toLowerCase().includes("coder"))
            setActiveArea("editor");
          if (resp.agentName?.toLowerCase().includes("file"))
            setActiveArea("files");
          if (resp.agentName?.toLowerCase().includes("preview"))
            setActiveArea("preview");
        }
      } else {
        console.error("Invalid response from workflow API:", data);
        toast.error("Agent workflow response is invalid.");
      }
      if (data.files && Array.isArray(data.files)) {
        for (const file of data.files) {
          await uploadFile({ filePath: file.filePath, content: file.content });
          openFile({
            id: Math.random().toString(36).substring(7),
            name: file.filePath.split("/").pop() || file.filePath,
            path: file.filePath,
            content: file.content,
            language: file.filePath.split(".").pop() || "text",
            type: "file",
          });
        }
        if (project?.id) await loadFiles(project.id);
      }
      toast.success("Workflow sent to agent(s)");
    } catch (error) {
      console.error("Workflow error:", error);
      addMessageToActiveChat({
        id: Math.random().toString(36).substring(7),
        content: "Workflow failed. Please try again.",
        sender: "ai",
        agentName: "System",
        timestamp: new Date(),
        type: "text",
      });
      toast.error("Failed to send workflow to agent(s)");
    } finally {
      setIsTyping(false);
    }
  };

  // Active Agent for dropdown label
  const activeAgent =
    availableAgents.find((a) => a.isActive) ||
    availableAgents.find((a) => a.name?.toLowerCase() === "coder") ||
    availableAgents[0];
  const agentLabel =
    activeAgent && activeAgent.name ? activeAgent.name : "Coder";

  return (
    <div className="flex flex-col h-full w-full max-w-full bg-user-bg text-user-text">
      {/* Agent Selection */}
      <div className="px-2 sm:px-4 md:px-6 py-4 border-b bg-user-bg shadow-sm flex items-center gap-4">
        <span className="text-sm text-user-text opacity-60 font-medium">
          Agent:
        </span>
        {availableAgents && availableAgents.length > 0 ? (
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button className="rounded-full px-4 py-2 bg-user-bg hover:bg-user-bg text-user-text border border-user-text shadow-sm transition flex items-center gap-2">
                <Bot className="w-4 h-4" />
                {agentLabel.length > 14
                  ? agentLabel.slice(0, 13) + "…"
                  : agentLabel}
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="start" className="w-48">
              {availableAgents.map((agent) => (
                <DropdownMenuItem
                  key={String(agent.id ?? "")}
                  onClick={() => toggleAgent(String(agent.id ?? ""))}
                  className={`flex items-center gap-2 ${
                    agent.isActive ? "bg-user-bg text-user-text" : ""
                  }`}
                >
                  <Bot className="w-4 h-4 text-user-text" />
                  <span className="truncate max-w-[200px]">
                    {agent.name || "Unnamed"}
                  </span>
                  {agent.isActive && (
                    <span className="ml-auto text-xs font-semibold text-user-text">
                      Active
                    </span>
                  )}
                </DropdownMenuItem>
              ))}
            </DropdownMenuContent>
          </DropdownMenu>
        ) : (
          <span className="text-red-500 text-xs">No agents available</span>
        )}
      </div>

      {/* Header */}

      {/* Chat Info */}

      {/* Messages Area */}
      <ScrollArea className="flex-1 px-2 sm:px-4 md:px-6 py-4 overflow-y-auto w-full max-w-full">
        {!activeChat && !isTyping ? (
          <div className="flex items-center justify-center h-32">
            <Spinner size={28} />
          </div>
        ) : (
          <div className="space-y-6 w-full max-w-3xl mx-auto px-2 sm:px-4 md:px-6">
            {messages
              .filter(
                (message) => message.content && message.content.trim() !== ""
              )
              .map((message) => (
                <ChatMessage key={message.id} message={message} />
              ))}
            {isTyping && (
              <TypingIndicator agentName={activeAgent?.name || "AI Agent"} />
            )}
            <div ref={bottomRef} />
          </div>
        )}
      </ScrollArea>

      {/* Input Area */}
      <div className="border-t bg-user-bg px-2 sm:px-4 md:px-6 py-4 shadow-inner w-full max-w-full">
        <div className="flex items-end gap-4">
          <Textarea
            rows={2}
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyDown={async (e) => {
              if (e.key === "Enter" && !e.shiftKey) {
                e.preventDefault();
                if (streaming) {
                  await handleSendMessageStream();
                } else {
                  await handleSendMessage();
                }
              }
            }}
            placeholder="Type your message..."
            className="flex-1 resize-none bg-code-bg rounded-lg px-4 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-user-text shadow-sm placeholder:text-muted-foreground text-user-text"
            spellCheck={false}
          />
          <Button
            size="icon"
            className="text-user-text rounded-full shadow-md hover:bg-user-bg transition"
            onClick={streaming ? handleSendMessageStream : handleSendMessage}
            disabled={isTyping || isStreaming}
            aria-label="Send Message"
          >
            <Send className="w-5 h-5" />
          </Button>
          <Button
            size="sm"
            className="bg-code-bg text-code-text px-5 py-2 rounded-md shadow hover:bg-code-bg"
            onClick={handleWorkflow}
            disabled={isTyping || isStreaming}
          >
            Run Workflow
          </Button>
          <Button
            variant={streaming ? "default" : "outline"}
            size="sm"
            className="ml-2 text-sm px-3 py-1.5 rounded bg-code-bg text-code-text border border-user-text"
            onClick={() => setStreaming((s) => !s)}
            aria-label="Toggle Streaming Mode"
          >
            {streaming ? "Streaming" : "Standard"}
          </Button>
        </div>
        <div className="text-xs text-user-text opacity-60 mt-2 flex justify-between select-none">
          <span>Enter to send, Shift+Enter for newline</span>
          <span>/clear /reset /help</span>
        </div>
      </div>

      {/* Streaming Text Preview */}
      {isStreaming && (
        <div className="p-3 text-sm mt-2 rounded max-w-3xl mx-auto">
          AI is typing...
        </div>
      )}
      {streamedText && <MarkdownMessage content={streamedText} />}
    </div>
  );
}

// Extract terminal command inside triple backticks with prefix 'terminal' (e.g. ```terminal\ncommand\n```)
function extractTerminalCommand(content: string): string | null {
  const regex = /```terminal\s*\n([\s\S]*?)```/i;
  const match = content.match(regex);
  if (match && match[1]) {
    return match[1].trim();
  }
  return null;
}
