"use client";

import React, { useRef, useEffect } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeHighlight from "rehype-highlight";
import { Copy } from "lucide-react";
import { motion } from "framer-motion";

type MarkdownMessageProps = {
  content: string;
  isAI?: boolean;
  onRender?: () => void;
};

export function MarkdownMessage({
  content,
  isAI = false,
  onRender,
}: MarkdownMessageProps) {
  const messageRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (onRender) onRender();
    messageRef.current?.scrollIntoView({ behavior: "smooth", block: "end" });
  }, [content, onRender]);

  return (
    <motion.div
      ref={messageRef}
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      className={
        isAI ? "bg-[#343541] text-white p-4 rounded-xl max-w-2xl shadow-md" : ""
      }
    >
      <div className="prose dark:prose-invert max-w-none prose-pre:bg-[#282c34] prose-pre:text-white prose-code:text-pink-400 prose-headings:font-semibold prose-h1:text-3xl prose-h2:text-2xl prose-p:leading-relaxed">
        <ReactMarkdown
          remarkPlugins={[remarkGfm]}
          rehypePlugins={[rehypeHighlight]}
          components={{
            code({ inline, className, children, ...props }) {
              const match = /language-(\w+)/.exec(className || "");
              const codeString = String(children).replace(/\n$/, "");

              if (!inline) {
                return (
                  <div className="relative group my-4">
                    <pre className="overflow-x-auto rounded-lg bg-[#282c34] p-4 text-sm text-white font-mono shadow-md">
                      <button
                        className="absolute top-2 right-2 opacity-0 group-hover:opacity-100 transition bg-[#23272f] text-white px-2 py-1 rounded text-xs"
                        onClick={() =>
                          navigator.clipboard.writeText(codeString)
                        }
                        title="Copy code"
                      >
                        <Copy className="inline w-4 h-4 mr-1" />
                        Copy
                      </button>
                      <code
                        className={`language-${match?.[1] || "plaintext"}`}
                        {...props}
                      >
                        {codeString}
                      </code>
                    </pre>
                  </div>
                );
              }

              return (
                <code className="bg-[#2d2d2d] text-pink-400 px-1 py-0.5 rounded text-sm font-mono">
                  {children}
                </code>
              );
            },
            h1: ({ children }) => (
              <h1 className="text-3xl font-bold mt-6 mb-3 text-white">
                {children}
              </h1>
            ),
            h2: ({ children }) => (
              <h2 className="text-2xl font-semibold mt-5 mb-3 text-gray-100">
                {children}
              </h2>
            ),
            h3: ({ children }) => (
              <h3 className="text-xl font-medium mt-4 mb-2 text-gray-200">
                {children}
              </h3>
            ),
            p: ({ children }) => {
              const isOnlyText = React.Children.toArray(children).every(
                (child: any) => typeof child === "string"
              );
              if (!isOnlyText) {
                return (
                  <div className="my-2 leading-relaxed text-gray-300">
                    {children}
                  </div>
                );
              }
              return (
                <p className="text-base leading-relaxed text-gray-300 my-2">
                  {children}
                </p>
              );
            },
            ul: ({ children }) => (
              <ul className="list-disc pl-6 my-2 text-gray-300">{children}</ul>
            ),
            ol: ({ children }) => (
              <ol className="list-decimal pl-6 my-2 text-gray-300">
                {children}
              </ol>
            ),
            li: ({ children }) => (
              <li className="my-1 text-gray-300">{children}</li>
            ),
            blockquote: ({ children }) => (
              <blockquote className="border-l-4 border-gray-600 pl-4 italic my-4 text-gray-400">
                {children}
              </blockquote>
            ),
            a: ({ children, href }) => (
              <a
                href={href}
                target="_blank"
                rel="noopener noreferrer"
                className="text-blue-400 underline hover:text-blue-300"
              >
                {children}
              </a>
            ),
            table: ({ children }) => (
              <table className="min-w-full border-collapse my-4">
                {children}
              </table>
            ),
            th: ({ children }) => (
              <th className="border-b border-gray-600 px-3 py-2 text-left font-semibold text-gray-200">
                {children}
              </th>
            ),
            td: ({ children }) => (
              <td className="border-b border-gray-700 px-3 py-2 text-gray-300">
                {children}
              </td>
            ),
          }}
        >
          {content}
        </ReactMarkdown>
      </div>
    </motion.div>
  );
}
